.userApply {
    .content {
        margin-bottom: 2rem;
        margin-right: 1rem;
        margin-top: 1rem;
        &___box {
            box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px, rgba(60, 64, 67, 0.15) 0px 1px 3px 1px;
            padding: 1rem 1.5rem 1rem 1.5rem;
            transition: 0.5s;
            margin-bottom: 1rem;
            .hr {
                height: 2px;
                width: 100px;
                background-color: green;
                margin-bottom: 10px;
            }
            &--title {
                font-size: 1.2rem;
                text-transform: capitalize;
            }
            &:hover {
                box-shadow: rgba(0, 0, 0, 0.25) 0px 14px 28px, rgba(0, 0, 0, 0.22) 0px 10px 10px;
                transform: translateY(-10px);
            }
            &---user {
                padding-bottom: 10px;
                .d-flex {
                    margin-bottom: 1rem;
                }
                &---infor {
                    margin-left: 1rem;
                    font-size: 1rem;
                    text-transform: capitalize;
                    color: black;
                    .td {
                        padding-right: 1rem;
                    }
                }
            }
        }
    }
    .btn-userApply {
        display: flex;
        gap: 10px;
        position: absolute;
        top: 10%;
        right: 0;
        .btn-link {
            position: unset;
        }
    }
    .box-textarea {
        width: 100%;
        resize: none;
    }
}